  padding: 5px 10px;
  border-radius: 2px;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}
.social-links a:after {
  position: absolute;
  left: 50%;
  bottom: 100%;
  margin-bottom: 8px;
  margin-left: -4px;
  content: "";
  border-width: 4px 4px 0 4px;
  border-style: solid;
  border-color: #444444 transparent transparent transparent;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}
.social-links a:hover:before,
.social-links a:hover:after {
  visibility: visible;
  opacity: 1;
}

